<template>
  <section class="mobile-menu">
      <div class="mobile-menu__content">
        <div class="mobile-menu__list">
          <div class="mobile-menu__item" v-for="item in menu" :key="item">
            <router-link :to="item.link" class="mobile-menu__link" active-class="menu__menu-link--active">
              <div class="mobile-menu__icon">
                <img :src="getIcon(item.icon)" :alt="item.name">
              </div>
              <div class="mobile-menu__name">{{ item.name }}</div>
            </router-link>
          </div>
        </div>
      </div>
  </section>
</template>

<script>
export default {
  name: "MobileMenu",
  data() {
    return {
      menu: [
        {
          name: "Поиск",
          icon: "image/search.svg",
          link: "/"
        },
        {
          name: "Продать",
          icon: "image/sell.svg",
          link: "/sell"
        },
        {
          name: "Чаты",
          icon: "image/chat.svg",
          link: "/chat"
        },
        {
          name: "Профиль",
          icon: "image/profile.svg",
          link: "/profile"
        },
      ]
    }
  },
  methods: {
    getIcon(iconPath)
    {
      return require('../assets/'+ iconPath)
    }
  }
}
</script>

<style lang="scss" scoped>
.mobile-menu{
  display: none;
  position: fixed;
  bottom: 0;
  width: 100%;
}
@media only screen and (max-width: 1023px) {
  .mobile-menu{
    display: block!important;
    &__content{
      background: whitesmoke;
      padding: 0px 26px;
    }
    &__list{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    &__item{
      padding: 12px 0 8px;
    }
    &__link{
      text-decoration: none;
    }
    &__icon{
      width: 16px;
      height: 16px;
      text-align: center;
      margin: 0 auto;
      img{
        width: 100%;
        display: block;
      }
    }
  }
}
</style>