import { createWebHistory, createRouter } from "vue-router";
import Home from "@/views/HomePage.vue";
import Sell from "@/views/SellPage.vue";
import Profile from "@/views/ProfilePage.vue";
import Chat from "@/views/ChatPage.vue";
import AuthedProfilePage from "@/views/AuthedProfilePage.vue";
import BalanceInOutPage from "@/views/BalanceInOutPage.vue";
import CategoryPage from "@/views/CategoryPage.vue";
import DisabledProductPage from "@/views/DisabledProductPage.vue";
import GamePage from "@/views/GamePage.vue";
import ProductPage from "@/views/ProductPage.vue";
import GamesListPage from "@/views/GamesListPage.vue";

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
        meta: {
            layout: "DefaultLayout"
        }
    },
    {
        path: "/sell",
        name: "Sell",
        component: Sell,
        meta: {
            layout: "DefaultLayout"
        }
    },
    {
        path: "/auth",
        name: "auth",
        component: Profile,
        meta: {
            layout: "DefaultLayout"
        }
    },
    {
        path: "/chat",
        name: "Chat",
        component: Chat,
        meta: {
            layout: "ChatLayout",
            title: "Chat"
        }
    },
    {
        path: "/profile",
        name: "Profile",
        component: AuthedProfilePage,
        meta: {
            layout: "PageLayout",
            title: "Profile"
        }
    },
    {
        path: "/balance",
        name: "Balance",
        component: BalanceInOutPage,
        meta: {
            layout: "PageLayout",
            title: "Balance"
        }
    },
    {
        path: "/category",
        name: "Category",
        component: CategoryPage,
        meta: {
            layout: "PageLayout",
            title: "Category"
        }
    },
    {
        path: "/disabled-products",
        name: "DisabledProducts",
        component: DisabledProductPage,
        meta: {
            layout: "PageLayout",
            title: "Disabled Products"
        }
    },
    {
        path: "/game",
        name: "GamePage",
        component: GamePage,
        meta: {
            layout: "PageLayout",
            title: "Game"
        }
    },
    {
        path: "/product",
        name: "Product",
        component: ProductPage,
        meta: {
            layout: "PageLayout",
            title: "Product"
        }
    },
    {
        path:"/apps",
        name:"Games",
        component: GamesListPage,
        meta: {
            layout:"PageLayout",
            title:"Games&apps"
        }
    },

];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;