<template>
  <div class="enter">
    <section class="sidebar__left">
      <div class="sidebar__container">
        <div class="sidebar__list">
          <MenuComponent/>
        </div>
      </div>
    </section>
    <component :is="layout">
      <slot />
    </component>
    <MobileMenu />
  </div>
</template>

<script>
import AppLayoutDefault from './DefaultLayout.vue'
import MenuComponent from "@/components/MenuComponent.vue";
import MobileMenu from "@/components/MobileMenu.vue";
export default {
  name: "AppLayout",
  components: {MobileMenu, MenuComponent},
  data: () => ({
    layout: AppLayoutDefault
  }),
  watch: {
    $route: {
      immediate: true,
      async handler(route) {
        try {
          const component = await import(`@/layout/${route.meta.layout}.vue`)
          this.layout = component.default || AppLayoutDefault
        } catch (e) {
          this.layout = AppLayoutDefault
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.enter{
  display: flex;
}
.sidebar{
  &__left{
    min-height: 100vh;
  }
  &__container{
    max-width: 220px;
    height: 100%;
  }
  &__list{
    display: flex;
    height: 100%;
  }
}
.mobile-menu{
  display: none;
}
@media only screen and (max-width: 1023px) {
  .sidebar{
    &__left{
      display: none;
    }
  }
  .enter{
    height: 100vh;
    flex-direction: column;
  }
}
</style>